import { PiStarFourBold } from "react-icons/pi";
import banner from "../Images/img/cushionbanner.png"
import controls1 from "../Images/img/controls1.png"
import controls2 from "../Images/img/controls2.png"
import arduino from "../Images/img/arduino.jpg"
export default function Pillow() {
    
    return <div className="">
      <img className="projectBanner" src={banner} alt="banner-cushion"/>
      <div className="projectitle">
      <h1>Kussen controler</h1>
        <h3>Kussen controller met game prototype voor patiënten.</h3>
        </div>
         <div className="skillsbanner">
                <span className="skillspan">
                    <PiStarFourBold />
                </span>
                <span className="skillspan">
                    Vaardigheden 
                </span>
                <span className="skillspan">
                    <PiStarFourBold />
                </span>
                <span className="skillspan">
                    Unity
                </span>
                <span className="skillspan">
                    Arduino
                </span>
            </div>
            <div className="projectcontent">
                <p>
                Afstudeer project gemaakt in samenwerking met het Kenniscentrum Rotterdam en Medical Delta Living Lab Better In – Better out.
                De casus van deze opdracht was om een concept te creëren voor patiënten die net een buik operatie zijnondergaan die hen zou motiveren tijdens hun verblijf in het ziekenhuis om in beweging te komen. 
                Door in beweging te komen, doormiddel van kleine acties zoals opzitten of opstaan kan dit hun herstel bevorderen maar wanneer een patient hersteld van een intensieve operatie. Vaak hebben zij vaak weinig energie en ervaren veel pijn.
                </p>
                <p>
                Tijdens deze opdracht heb ik onderzoek uitgevoerd binnen het ziekenhuis en een werkend prototype gemaakt voor het concept van een kussen die als game controller werkt.
                </p>
                <p>
                De gebruiker kan een spel spelen door middel van deze kussen. Door naar voren, achter, links en rechts te leunen kunnen ze de character heen en weer bewegen. Hiervoor heb ik een prototype gecreëerd waarbij je een vliegtuig bestuurd. 
                </p>
                <img className="projectImage" src={controls1} alt="controls"/>
                <img className="projectImage" src={controls2} alt="controls"/>
                <p>Het kussen is gemaakt door middel van druk sensoren en de Arduino. De sensoren zijn geplakt op een dun hout oppervlak en deze is geplaatst in een kussensloop met schuim boven op.</p>
                <p>De game is gemaakt door van middel Unity.</p>
                <img className="projectImage" src={arduino} alt="arduino"/>
            
            <h3>Links</h3>
            <ul className="callList">
                <li>
                    <h3>
                        <a className="callA" href="https://github.com/lfdejong/graduation">Code</a>
                    </h3>
                </li>
            </ul>
            </div>
    </div>;
  }
  